.aa-header{
    width: 100%;
    height: 80px;
    display: flex;            
    justify-content: center;
    align-items: center;
    border-bottom: 1px solid #ccc;

    .aa-header__warpper{
        padding: 0 16px;
        flex-grow: 1;
    }

    .aa-header__xl__serch{
        position: relative;
        
        .aa-serch-input{
            width: 100%;
            padding: 8px 10px 8px;
            border: 1px solid #ccc;
            border-radius: 20px 20px 20px 20px;
            outline: 0;            
        }
    
        .aa-search-icon{
            width: 24px;
            height: 24px;
            position: absolute;
            top: 8px;
            right: 8px;
            padding: 0;
            outline: 0;
            background-color: transparent;
            border: 0;
        }
    }

    .aa-header__xl__btn-category{
        position: relative;
        align-items: center;
        border: none;
        cursor: pointer;
        display: inline-flex;
        min-width: 8rem;
        padding: 0 1.2rem;
        justify-content: center;
        user-select: none;
        vertical-align: bottom;
        white-space: nowrap;
        color: #5624d0;
        background-color: transparent;
        min-width: auto;
        padding: 0 1.2rem;
    }

    .aa-btn {
        text-decoration: none;
        height: 40px;
        position: relative;
        align-items: center;
        display: inline-flex;
        min-width: 8rem;
        padding: 0 1.2rem;
        justify-content: center;
        user-select: none;
        vertical-align: bottom;
        color: #5624d0;
        border: 1px solid #5624d0;
        cursor: pointer;
        background-color: transparent;
        min-width: auto;
        outline: 0;
    }
    
    .aa-btn-signup{
        background-color: black;
        color: #fff;
    }
    
    .aa-btn-signup:hover{
        background-color: rgb(14, 13, 13);
        color: #fff!important;
    }

    .aa-btn-signin:hover{
        color: #2d0591;
        border: 1px solid #2d0591;
        background-color: rgb(216, 216, 216);
    }
    
    .aa-category-menu{
        width: 130px;
        display: inline-block;
        padding: 0px;
        position: absolute;
        top: 80px;
        left: 140px;
        /* border: 1px solid; */

        .menu-item{
            position: relative;
            color: #fff;
            padding: 0 4px;
            margin: 0 -8px;
        } 
        .menu-item :hover{
            background: #3f51b5;
            color: #fff;
            position: relative;
        } 
        .has-submenu{
            position: relative;
        }
        
        .sub-menu{
            position: absolute;
            width: 130px;
            top: 0px;
            left: 121px;
            border: 1px solid;
            padding: 8px;
        }
    }
}


.aa-video{
    .aa-video__class-list__item{
        display: inline-block;
        padding: 4px;
        border: 1px solid #ffffff00;
    }
    .aa-video__class-list__item:hover{
        border: 1px solid #b2b3b5;
    }

    .aa-video__class-list__title{
        padding-left: 8px;
    }
}


.aa-profile{
    max-width: 1000px; 
    margin: 50px auto 0;

    .btn-follow {
        text-rendering: optimizeLegibility;
        -webkit-text-size-adjust: 100%;
        margin: 0;
        font: inherit;
        vertical-align: baseline;
        display: inline-block;
        text-align: center;
        -webkit-font-smoothing: antialiased;
        -webkit-appearance: none;
        outline: none;
        font-weight: 700;
        white-space: nowrap;
        font-family: gt walsheim pro, helvetica, arial, sans-serif;
        border: 2px solid #3722d3;
        box-sizing: border-box;
        color: #3722d3;
        border-radius: 20px;
        text-decoration: none;
        font-size: 15px;
        line-height: 20px;
        padding: 6px 13px 7px;
        min-width: 75px;
    }


    .btn-follow:hover {
        border: 2px solid #3722d3;
        color: #3722d3;
    }

    .follow-stats .follow-stats-wrapper-user-stats .number {
        font-size: 22px;
        font-weight: 700;
    }

    .follow-stats .follow-stats-wrapper-user-stats .follow-stat {
        font-weight: 400;
    }

    .am-user-profile-info {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 20px;
    }

    .am-user-profile-info .am-info {
        display: flex;
        flex-direction: column;
    }

    .follow-stats-wrapper-user-stats a {
        margin-right: 0.5rem;
        margin-left: 0.5rem;
    }

    @media screen and (min-width:992px) {
        .am-user-profile-info {
            display: block;
        }

        .am-user-profile-info .am-info {
            flex-direction: column-reverse;
        }
    }
}